<!--  -->

<template>
  <div class="reader-container">
    <!--  -->
    <aside class="sidebar">
      <div class="sidebar-header">
        <h3 @click="jumpto('/articles/content')"></h3>
      </div>
      <div class="toc-content">
        <div v-if="loading" class="loading">
          <div class="loading-spinner"></div>
          <span>...</span>
        </div>
        <div v-else-if="error" class="error">
          {{ error }}
        </div>
        <nav v-else-if="toc.length > 0" class="toc">
          <ul>
            <li v-for="(item, index) in toc" :key="index" 
                :class="['toc-item', `level-${item.level}`]" 
                @click="scrollToHeading(item.id)">
              <span class="toc-link">{{ item.text }}</span>
            </li>
          </ul>
        </nav>
        <div v-else class="empty-toc">
          
        </div>
      </div>
    </aside>

    <!--  -->
    <main class="content">
      <div class="markdown-container">
        <div v-if="loading" class="loading">
          <div class="loading-spinner"></div>
          <span>...</span>
        </div>
        <div v-else-if="error" class="error">
          {{ error }}
        </div>
        <div v-else-if="fileContent" v-html="renderedMarkdown"></div>
        <div v-else class="empty-content">
          
        </div>
      </div>
    </main>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, watch } from 'vue';
import { useRouter } from 'vue-router';
import { marked } from 'marked';

const router = useRouter();

const fileUrl = ref('');
const fileContent = ref('');
const loading = ref(false);
const error = ref('');
const toc = ref([]);
const renderedMarkdown = ref('');

// 
const jumpto = (path) => {
  router.push(path);
};



// JavaScriptURL
function getURLParams() {
  const urlParams = new URLSearchParams(window.location.search);
  const fileParam = urlParams.get('file');
  
  if (fileParam) {
    fileUrl.value = fileParam;
    console.log(':', fileParam);
    loadFileContent();
  } else {
    console.log('');
    // markdown
    fileContent.value = `# Markdown
## 



### 

- Markdown
- 
- 
- 

## 

1. URLMarkdown
2. 
3. `;
  }
}

// 
async function loadFileContent() {
  if (!fileUrl.value) {
    error.value = '';
    return;
  }
  
  loading.value = true;
  error.value = '';
  fileContent.value = '';
  
  try {
    // fetch API
    const response = await fetch(fileUrl.value);
    
    if (!response.ok) {
      throw new Error(`: ${response.status} ${response.statusText}`);
    }
    
    // 
    const content = await response.text();
    fileContent.value = content;
  } catch (err) {
    // 
    error.value = `: ${err.message}`;
    console.error(':', err);
  } finally {
    loading.value = false;
  }
}

// Markdown
function generateTOC(markdownContent) {
  const tocItems = [];
  const headings = markdownContent.match(/^(#{1,6})\s+(.*?)$/gm);
  
  if (headings) {
    headings.forEach((heading, index) => {
      const levelMatch = heading.match(/^#{1,6}/);
      const textMatch = heading.match(/^#{1,6}\s+(.*?)$/);
      
      if (levelMatch && textMatch) {
        const level = levelMatch[0].length;
        const text = textMatch[1].replace(/[*\[\]()]/g, '').trim();
        const id = `heading-${index}`;
        
        tocItems.push({
          id,
          text,
          level
        });
      }
    });
  }
  
  return tocItems;
}

// MarkdownID
function renderMarkdownWithIds(markdownContent) {
  const tocItems = generateTOC(markdownContent);
  toc.value = tocItems;
  
  let rendered = marked(markdownContent);
  
  // HTMLID
  tocItems.forEach((item, index) => {
    const regex = new RegExp(`<h${item.level}>(.*?)<\/h${item.level}>`);
    rendered = rendered.replace(regex, `<h${item.level} id="${item.id}">$1<\/h${item.level}>`);
  });
  
  return rendered;
}

// 
const scrollToHeading = (id) => {
  const element = document.getElementById(id);
  if (element) {
    element.scrollIntoView({ behavior: 'smooth', block: 'start' });
  }
};

// fileContentMarkdown
watch(fileContent, (newContent) => {
  if (newContent) {
    renderedMarkdown.value = renderMarkdownWithIds(newContent);
  }
});

// URL
onMounted(() => {
  getURLParams();
});
</script>

